<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Add Address Information</h1>
      <div class="chartWrap">
        <a class="iconfont cart"> &#xe669; </a></div>
    </section>
    <div class="inputWrap">
        <div class="coat"><div class="label">First Name<font>*</font></div> <van-field v-model="form.firstName" /></div>
        <div class="coat"><div class="label">Last Name<font>*</font></div> <van-field v-model="form.lastName" /></div>
        <div class="country">
            <div class="coat_left">
                <div class="label">Country</div> <van-field @click="showPicker(0)" :required="true" right-icon="arrow-down" v-model="form.countryName" />
            </div>
            <div class="coat_right">
                 <div class="label">City</div> <van-field :required="true" right-icon="arrow-down" v-model="form.cityName" />
            </div>
        </div>
        <div class="region"><div class="label">Region</div> <van-field :required="true" right-icon="arrow-down" v-model="value" /></div>
        <div class="coat"><div class="label">Street and Building</div> <van-field :required="true" v-model="form.street" /></div>
        <div class="coat"><div class="label">Apartment and Floor</div> <van-field v-model="form.apartment" /></div>
        <div class="coat"><div class="label">Nearest Landmark</div> <van-field v-model="form.landmark" /></div>
        <div class="comment">
            <p>Address Type</p>
            <div :class=" [{red:typeIndex==i}, 'setmeal']" v-for='(item,i) in typeList' :key='item.name' @click='typeIndex=i'>
                {{item.name}}
            </div>
                <!-- <div class="setmeal">
                Home
            </div>
                <div class="setmeal">
                Work
            </div>
                <div class="setmeal">
                School
            </div> -->
            <div style="clear: both;"></div>
        </div>
        <div class="coat"><div class="codeName">+97</div><div class="label">Mobile Number<font>*</font></div> <van-field v-model="form.mobile" /></div>
        <div class="coat"><div class="codeName">+97</div><div class="label">Landline Number</div> <van-field v-model="form.landlineNumber" /></div>
        <van-checkbox v-model="checked" checked-color="red">Set as default address</van-checkbox>
        <van-button type="danger" @click='confirm'>Sign In</van-button>
    </div>
    <div class="pickerWrap" v-show="showPick">
       
    </div>
     <div class="picker">
        <van-picker :columns="pickList" ref="picker" @change="onChange" v-show="showPick" />
    </div>
    
</div>
</template>

<script>

import {addAddress,deleteAddress,country,city} from '@/api/address';
export default {
  name: 'addAddress',
  data () {
    return {
        countryList:[],
        cityList:[],
        pickList:[],
        regionList:[],
        showPick:false,
        value:'',
        checked: false ,
        typeIndex:0,
        typeList:[
            {name:'None',bol:true},
            {name:'Home',bol:false},
            {name:'Work',bol:false},
            {name:'School',bol:false}
            ],
        form:{
            //联系人姓
            firstName:'',
            //联系人名
            lastName:'',
            // 国家
            countryName:'',
            // 国家编号
            countryCode:'123',
            //城市编号
            provinceCode:'123',
            //省名
            provinceName:'',
            //城市编号
            cityCode:'132',
            //城市名
            cityName:'',
            //街道
            street:'',
            //建筑
            building:'',
            //手机号
            mobile:'',
            //房间号
            apartment:'',
            //地址标识
            landmark:''
        }
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
        // this.$store.dispatch('getUserInfo').then(res=>{
        //     this.$toast(JSON.stringify(res))
        // });

        country().then(res=>{
            this.countryList=res.data.result;
        });
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    confirm(){
       addAddress(this.form) .then(res=>{

           this.$router.back(-1)
       })
    },
    onChange(){

    },
    showPicker(index){
        this.pickList=this.countryList.map(c=>{return c.countryCode;})
        this.showPick=true;
    }


  }
}
</script>

<style lang="scss" scoped>

   .head{
        background-color: #f6f6f6;
        padding: 0 15px;
    }
   .inputWrap{
       width: 100%;
       padding-left: 4%;
       .coat{
           width: 100%;
           display: -webkit-box;
           border-bottom: #f6f6f6 1px solid;
           height: 50px;
           .label{
               color: #999999;
           }
           .codeName{
               display: flex; align-items: center;
               padding-right: 25px;
               font-size: 14px;
           }
           .van-cell{
                   padding: 10px 0 10px 15px;
                   width: 202px;
            }         
       }
       font{
           color: red;
       }
   }
   .label{
       display: flex; align-items: center;
   }
    .comment{
      width: 100%;
      padding-bottom: 15px;
      border-bottom: #f6f6f6 1px solid;
      p{
          padding-top: 14px;
      }
    }
    .setmeal{
        width: 61px;
        height: 27px;
        font-size: 14px;
        line-height: 27px;
        border: #666666 1px solid;
        border-radius: 2px;
        text-align: center;
        color: #666666;
        float: left;
        margin-top: 11px;
        margin-right: 15px;
    }
    .red{
      border: #E33946 1px solid;
      color: #E33946;
    }
    .region{
         width: 100%;
           display: -webkit-box;
           border-bottom: #f6f6f6 1px solid;
           .label{
               color: #999999;
           }
           .van-cell{
                   padding: 10px 0 10px 15px;
                   width: 282px;
            }
    }
    .country{
        width: 100%;
        color: #999999;
        display: flex;
        justify-content: space-between;
        .coat_left{
            width: 48%;
            display: -webkit-box;
            border-bottom: #f6f6f6 1px solid;  
            .van-cell{
                padding: 10px 15px;
                width: 125px;
                .van-field__clear, .van-field__right-icon{
                    margin: 0;
                }
            }
        }
        .coat_right{
            width: 48%;
            display: -webkit-box;
            border-bottom: #f6f6f6 1px solid;
            .van-cell{
                padding: 10px 10px 10px 15px;
                width: 125px;
            }
        }
    }
    .van-checkbox{
        padding-top: 10px;
        font-size: 12px;
        color: #666666;
    }
    .van-button{
      width: 96%;
      margin: 91px 4% 67px 0 ;
    }
    .picker{
        position: fixed;
        bottom: 0;
        width: 100%;
        z-index: 120;
    }
    .pickerWrap{
        width: 100%;
        height: 100%;
        opacity: 0.5;
        background-color: rgba(0,0,0,.7);
        z-index: 100;
        position: fixed;
        top: 0;
    }
</style>
<style lang="scss">
    .inputWrap{
        .van-checkbox__label{
            color: #999999;
        }  
    }
</style>



