import request from '@/common/js/fetch'

//获取地址
export function getAddress() {
    return request({
      url: '/address/get',
      method: 'get'
    })
  }
  //我的地址
  export function myAddress() {
    return request({
      url: '/address/myAddress',
      method: 'get'
    })
  }

//新增地址
export function addAddress(params) {

    return request({
      url: '/address/add',
      method: 'post',
      params
    })
  }
  //修改地址
  export function editAddress(params) {

    return request({
      url: '/address/edit',
      method: 'post',
      params
    })
  }
  //删除地址
  export function deleteAddress(id) {
    const data={
      id
    }
    return request({
      url: '/address/delete',
      method: 'post',
      params:data
    })
  }


  export function country() {
    return request({
      url: '/address/country',
      method: 'get'
    })
  }

  export function city(params) {
    return request({
      url: '/address/country',
      method: 'get',
      params
    })
  }


